export default {
    name: 'app-select',
    props: {
        list: {
            type: Array,
            default: function() {
                return [];
            }
        },
        isShow: {
            type: Boolean,
            default: false,
        },
        title: {
            type: String,
            default: ''
        },
        height: {
            type: String,
            default: '440rpx',
        },
        index: {
            type: Number,
            default: 0
        }
    },
    watch: {
        isShow(newVal, oldVal) {
            if (newVal) {
                this.newValue = this.index;
            }
        }
    },
    data() {
        return {
            newValue: 0,
            value: [0],
            indicatorStyle: `height: ${Math.round(uni.getSystemInfoSync().screenWidth/(750/88))}px;`
        }
    },
    methods: {
        confirm(type) {
            this.$emit('confirm', { index: this.newValue, is_modal_confirm: type === 'close' });
        },
        change(e) {
            this.newValue = e.detail.value[0];
        }
    }
}
